import React from 'react'
import styled from 'styled-components'
import Pagination from '@atlaskit/pagination'

const PaginationContainer = styled.div`
  margin: 30px 0;
  display: flex;
  justify-content: center;
`

const ParserResultPagination = ({pagination, selectPage}) => {
  const pages = Array.from({length: pagination.last_page}, (v, k) => k + 1)

  return (
    <PaginationContainer>
      <Pagination
        pages={pages} 
        selectedIndex={pagination.current_page - 1}
        onChange={(e, page) => selectPage(`/pages?page=${page}`)}
      />
    </PaginationContainer>
  )
}

export default ParserResultPagination