import * as React from 'react'

import QuizTitle from './components/QuizTitle'
import QuizHeader from './components/QuizHeader'
import { Pagination, PaginationItem } from './components/Pagination'
import QuizWrap from './components/QuizWrap'
import ButtonStart from './components/ButtonStart'
import ImageBlock from './components/ImageBlock'

const transformColor = (color) => color === '#000' ? '0,0,0' : '255,255,255'

const StartPage = ({
  title,
  teaserImage,
  preview,
  questionItems, result,
  handleStartQuestion,
  handleOnPage,
  handleOnPageResult,
  teaserBackground,
  options, block, header, button,
  styles
}) => {

  const color = transformColor(options.startBackground || (teaserBackground && teaserBackground.color));
  questionItems = options.questions || questionItems;
  block = styles.block || block;
  header = styles.header || header;
  button = styles.button || button;
  
  return (
    <>
      <QuizWrap
        border={block.border}
        borderRadius={block.borderRadius}
      >
        {
          options.title || title ?
          <QuizTitle 
            fontSize={header.fontSize}
            fontFamily={header.fontFamily}
            fontWeight={header.fontWeight}
            lineHeight={header.lineHeight}
            color={header.color}
          >
            {options.title || title}
          </QuizTitle> : undefined
        }
        <QuizHeader background={`rgba(${color}, ${options.startOpacity || (teaserBackground ? teaserBackground.opacity : 0)})`}>
          <ImageBlock>
            <img src={options.startImage || teaserImage} alt=""/>
          </ImageBlock>
          <ButtonStart
            normal={button.normal}
            hover={button.hover}
            onClick={handleStartQuestion}
          >
            Start
          </ButtonStart>
          <div className="teaser-image-fade"/>
        </QuizHeader>
      </QuizWrap>
      <Pagination>
        <PaginationItem border={!preview && !result && 'solid 2px blue'}>start</PaginationItem>
        {
          questionItems && questionItems.map((item, index) => (
            <PaginationItem
              key={item.id}
              onClick={() => handleOnPage(index + 1)}
            >
              {index + 1}
            </PaginationItem>
          ))
        }
        <PaginationItem onClick={handleOnPageResult}>results</PaginationItem>
      </Pagination>
    </>
  )
}
  
export default StartPage