import * as React from 'react'
import {
  ErrorMessage,
  HelperMessage,
  ValidMessage,
} from '@atlaskit/form'


interface IFieldMessage {
  error?: string
  helpMessage?: string
  validMessage?: string
}

const FieldMessage = ({error, helpMessage, validMessage}: IFieldMessage) =>
<>
  {!error && !validMessage && (
    <HelperMessage>{helpMessage}</HelperMessage>
  )}
  {error && (
    <ErrorMessage>{error}</ErrorMessage>
  )}
  {validMessage && (
    <ValidMessage>{validMessage}</ValidMessage>
  )}
</>

export default FieldMessage;