import styled from 'styled-components'

const CodeButton = styled.button`
  color: #fff;
  border: none;
  padding: 8px 15px;
  border-radius: 3px;
  background: #0052CC;
  cursor: pointer;
  outline: none;

  &:disabled {
    cursor: not-allowed;
    color: #B2B2B2;
  } 
  &:hover {
    background: #0065FF;
  }
`
export default CodeButton