import * as React from 'react'

import QuizWrap from './components/QuizWrap'
import QuestionsCont from './components/QuestionsCont'
import Question from './components/Question'
import QuizTitle from './components/QuizTitle'
import ImageBlock from './components/ImageBlock'
import QuizHeader from './components/QuizHeader'
import  Dot, { DotContainer } from './components/Dot'
import Variant, { VariantItems }  from './components/Variant'
import Next, { ButtonNext } from './components/ButtonNext'
import { Pagination, PaginationItem } from './components/Pagination'

const QuestionsPage = ({
  questionItems,
  selectedQuestionIndex,
  title,
  rightAnswer,
  wrongAnswer,
  handleSelectAnswer,
  handleChangeQuiz,
  handleOnPageStart,
  handleOnPage,
  handleOnPageResult,
  options, block, header,
  paragraph, button,
  styles
}) => {

  questionItems = options.questions || questionItems;
  block = styles.block || block;
  header = styles.header || header;
  paragraph = styles.paragraph || paragraph;
  button = styles.button || button;
  
  return (
    <>
    <QuizWrap
      border={block.border}
      borderRadius={block.borderRadius}
    >
      <QuestionsCont>
      {
        questionItems.map((item, index) => (
          index + 1 === selectedQuestionIndex &&
          <React.Fragment key={item.id}>
            <QuizTitle
              fontSize={header.fontSize}
              fontFamily={header.fontFamily}
              fontWeight={header.fontWeight}
              lineHeight={header.lineHeight}
              color={header.color}
            >
              {options.title || title}
            </QuizTitle>
            {
              item.image &&
              <QuizHeader>
                <ImageBlock>
                  <img src={item.image} alt="" />
                </ImageBlock>
              </QuizHeader>
            }
            <Question
              fontSize={paragraph.fontSize}
              fontFamily={paragraph.fontFamily}
              color={paragraph.color}
            >
              {item.question}
            </Question>
            <DotContainer>
              {
                questionItems.map((item, index) => (
                  <Dot
                    border={button.normal.background}
                    key={item.id}
                    background={index + 1 === selectedQuestionIndex && button.normal.background}
                  />
                ))
              }
            </DotContainer>
            <VariantItems>
              {
                item.answers.map((answer) => (
                  answer.value &&
                  <Variant
                    key={answer.id}
                    normal={paragraph}
                    hover={button.hover}
                    answerBorder={answer.id === rightAnswer ? '0 0 10px 0 inset green' :
                                  answer.id === wrongAnswer && '0 0 10px 0 inset red' }
                    onClick={() =>
                      rightAnswer === null && wrongAnswer === null &&
                      handleSelectAnswer(answer.id, item.indexRightAnswer)
                    }
                  >
                    {answer.value}
                  </Variant>
                ))
              }
            </VariantItems>
            <ButtonNext>
              <Next
                type="button"
                normal={button.normal}
                hover={button.hover}
                onClick={handleChangeQuiz}
                disabled={rightAnswer === null && wrongAnswer === null && true}
              >
                Next
              </Next>
            </ButtonNext>
          </React.Fragment>
        ))
      }
      </QuestionsCont>
     </QuizWrap>
      <Pagination>
        <PaginationItem onClick={handleOnPageStart}>start</PaginationItem>
          {
            questionItems.map((item, index) => (
              <PaginationItem
                key={item.id}
                onClick={() => handleOnPage(index + 1)}
                border={index + 1 === selectedQuestionIndex && 'solid 2px blue'}
              >
                {index + 1}
              </PaginationItem>
            ))
          }
        <PaginationItem onClick={handleOnPageResult}>results</PaginationItem>
      </Pagination>
   </>
  )
}

export default QuestionsPage
