import * as React from 'react'

import ResultPage from './ResultPage'
import StartPage from './StartPage'
import QuestionsPage from './QuestionsPage'

class Quiz extends React.Component  {

  state = {
    preview: false,
    selectedQuestionIndex: 1,
    rightAnswer: null,
    wrongAnswer: null,
    countRightAnswer: 0,
    result: false,
    options: {},
    base: {},
    block: {},
    header: {},
    button: {},
    paragraph: {},
  }

  componentWillMount = () => {
    const htmlProps = window.funlib;
    if (htmlProps && htmlProps.isEmbed) {
      this.setOptions(htmlProps);
    }
  }

  setOptions = (htmlProps) => {
    this.setState({
      options: htmlProps.options,
      base: htmlProps.base,
      block: htmlProps.block,
      header: htmlProps.header,
      button: htmlProps.button,
      paragraph: htmlProps.paragraph,
    });
  }

  handleStartQuestion = () => {
    this.setState({preview: true})
  }

  handleOnPageStart = () => {
    this.setState({
      preview: false,
      selectedQuestionIndex: 1,
      result: false,
      rightAnswer: null,
      wrongAnswer: null,
      countRightAnswer: 0,
    })
  }

  handleOnPageResult = () => {
    this.setState({
      preview: false,
      result: true,
    })
  }

  handleOnPage = (id) => {
    this.setState({
      preview: true,
      result: false,
      rightAnswer: null,
      wrongAnswer: null,
      selectedQuestionIndex: id,
    })
  }

  handleChangeQuiz = () => {
    const questionItems = this.props.questionItems || this.state.options.questions;

    if (this.state.selectedQuestionIndex >= questionItems.length) {
      this.setState({
        result: true,
        preview: false
      })
    }else {
      this.setState({
        rightAnswer: null,
        wrongAnswer: null,
        selectedQuestionIndex: this.state.selectedQuestionIndex + 1
      })
    }
  }

  handleSelectAnswer = (index, indexRightAnswer) => {
    const questionItems = this.props.questionItems || this.state.options.questions;

    if (index === indexRightAnswer) {
      this.setState({
        rightAnswer: index,
        countRightAnswer: this.state.countRightAnswer < questionItems.length ?
          this.state.countRightAnswer + 1 : this.state.countRightAnswer + 0
      })
    } else {
      this.setState({
        rightAnswer: indexRightAnswer,
        wrongAnswer: index
      })
    }
  }

  render() {
    const {
      teaserImage, title, questionItems,
      teaserBackground, finalTeaserImage,
      finalTeaserBackground, styles
    } = this.props;

    const {
      preview, selectedQuestionIndex, rightAnswer,
      wrongAnswer, countRightAnswer, result,
      options, block, header, button, paragraph, //base
    } = this.state;
    
    if (preview) {
      return (
        <QuestionsPage
          questionItems={questionItems}
          selectedQuestionIndex={selectedQuestionIndex}
          title={title}
          rightAnswer={rightAnswer}
          wrongAnswer={wrongAnswer}
          handleSelectAnswer={this.handleSelectAnswer}
          handleChangeQuiz={this.handleChangeQuiz}
          handleOnPageStart={this.handleOnPageStart}
          handleOnPage={this.handleOnPage}
          handleOnPageResult={this.handleOnPageResult}
          options={options}
          block={block}
          header={header}
          paragraph={paragraph}
          button={button}
          styles={styles}
        />
      )
    }else if (result) {
      return (
        <ResultPage
          title={title}
          finalTeaserImage={finalTeaserImage}
          countRightAnswer={countRightAnswer}
          questionItems={questionItems}
          result={result}
          handleOnPageStart={this.handleOnPageStart}
          handleOnPage={this.handleOnPage}
          finalTeaserBackground={finalTeaserBackground}
          options={options}
          block={block}
          header={header}
          button={button}
          styles={styles}
        />
      )
    }else {
      return (
        <StartPage
          title={title}
          teaserImage={teaserImage}
          questionItems={questionItems}
          preview={preview}
          teaserBackground={teaserBackground}
          handleStartQuestion={this.handleStartQuestion}
          handleOnPage={this.handleOnPage}
          handleOnPageResult={this.handleOnPageResult}
          options={options}
          block={block}
          header={header}
          button={button}
          styles={styles}
        />
      )
    }
  }
}

export default Quiz
