import * as React from 'react'
import Select from '@atlaskit/select';
import ColorPicker from '@atlaskit/color-picker';
import styled from 'styled-components'

import StyleContainer from '../StyleContainer'
import SelectContainer from '../StyleSelectContainer'
import ColorPickerContainer from '../ColorPickerContainer'
import TitleContainer from '../StyleTitle'


// for checking 
const RowDiv = styled.div`
  display: flex;
  align-items: center;
  border-bottom: 1px solid #B6B6B6;
  padding-bottom: 10px;
`

const StyleChoose = ({title, background, styles, setColor, setStyle}) => {
  const [SelectedColor, setSelectedColor] = React.useState();
  return (
    <StyleContainer>
      <TitleContainer>{title}</TitleContainer>
      <RowDiv>
        <ColorPickerContainer>
          {
            background.length > 0 && 
            <ColorPicker
              label="Change color"
              palette={background}
              selectedColor={SelectedColor}
              onChange={(newColor) => {
                setColor(newColor, title);
                setSelectedColor(newColor);
              }}
            />
          }
        </ColorPickerContainer>
        <SelectContainer>
          { 
            styles && 
            <Select
              placeholder="Select a style for the item."
              options={styles}
              onChange={value => setStyle(value, title)}
            /> 
          }
        </SelectContainer>
      </RowDiv>
    </StyleContainer>
  );
}


export default StyleChoose
