import styled from 'styled-components'
//import { withButtonStyles } from '../../withDefinedStyles'

export const VariantItems = styled.ul`
  display: flex;
  flex-direction: column;
  padding-left: 0;
  min-width: 70%;
  list-style: none;
`

const Variant = styled.li`
  font-family: ${({normal: {fontFamily}}) => fontFamily || 'inherit'};
  font-size: ${({normal: {fontSize}}) => fontSize || '16px'};
  font-weight: ${({hover: {fontWeight}}) => fontWeight || 'bold'};
  color: ${({normal: {color}}) => color || "#8a8888"};
  box-shadow: ${({answerBorder}) => answerBorder || 'none'};
  background: #e9e9e9;
  text-align: center;
  word-wrap: break-word;
  padding: 15px 10px;
  margin: 2px 0;
  cursor: pointer;
  transition: .2s;

  &:hover {
    background: #F1F1F1;
    color: ${({hover: {color}}) => color || '#fff'};
  }
`

export default Variant 
