import styled from 'styled-components'

const QuizHeader = styled.div`
  width: 100%;
  position: relative;
  padding-bottom: 56.25%;
  /*Ths border was why the pixels moved right*/
  /*border: solid 1px #d1d1d1;*/
  box-sizing: border-box;
  
  .teaser-image-fade{
    background: ${({background = 'rgba(0,0,0,0.5)'}) => background};
    width: 100%;
    height: 100%;
    z-index: 1;
    position: absolute;
    top: 0;
  }
  
  .button{
    z-index: 2;
  }
`

export default QuizHeader
