import React, { useState } from 'react'

import Container from '../components/WidthContainer'
import AppMain from '../components/AppMain'
import { IRegexField } from '../components/Field'
import Form from '../components/Form'
import FormWrapp from '../components/FormWrapp'
import AuthService from '../components/Auth/AuthService'

const singUp: IRegexField[] = [
  {
    fieldName: 'name',
    validationType: 'name',
    label: 'Name',
    validOnType: true,
    helpMessage: 'Name must contain at least 3 characters',
    textFieldProps: {
      type: 'text',
    },
    fieldProps: {
      isRequired: true,
    },
  },
  {
    fieldName: 'email',
    validationType: 'email',
    label: 'Email',
    validOnType: true,
    validMessage: 'Email is valid',
    helpMessage: 'Email example: username@your.name',
    textFieldProps: {
      type: 'text',
    },
    fieldProps: {
      isRequired: true,
    },
    regex: /^(([^<>()\\[\]\\.,;:\s@‚Äú]+(\.[^<>()\\[\]\\.,;:\s@‚Äú]+)*)|(‚Äú.+‚Äú))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
  },
  {
    fieldName: 'password',
    validationType: 'password',
    fieldProps: {
      isRequired: true,
    },
    helpMessage: 'Use from 6 to 50 characters with at least one uppercase and one number',
    label: 'Password',
    validOnType: true,
    validMessage: 'Password is valid',
    textFieldProps: {
      type: 'password',
    },
    regex: /(?=.*[0-9])(?=.*[a-z])(?=.*[A-Z])[0-9a-zA-Z]{6,}/
  },
  {
    fieldName: 'password_confirmation',
    validationType: 'password_confirmation',
    fieldProps: {
      isRequired: true,
    },
    helpMessage: 'Passwords must be the same',
    label: 'Confirm Password',
    validOnType: true,
    validMessage: 'Passwords match',
    textFieldProps: {
      type: 'password',
    },
    regex: /(?=.*[0-9])(?=.*[a-z])(?=.*[A-Z])[0-9a-zA-Z]{6,}/
  }
]

const SignUpPage = ({history}) => {
  const Auth = new AuthService();
  const [error, setError] = useState();

  return(
    <AppMain>
    <Container>
      <FormWrapp>
        <Form
          formName="SIGN UP"
          fields={singUp}
          registerError={error}
          onSubmit={(formData: any) => Auth.toRegister(formData)
            .then(response => {
              if(response) {
                history.replace('/');
              }
            })
            .catch(error => {
              setError(error.message);
            })
          }
        />
      </FormWrapp>
    </Container>
  </AppMain>
  )
}

export default SignUpPage