import styled from 'styled-components'
//import { withButtonStyles } from '../../withDefinedStyles'

const Result = styled.div`
  position: absolute;
  z-index: 2;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: ${({normal: {color}}) => color || '2a2a2a'};
  background: ${({normal: {background}}) => background || '#ddd'};
  font-size: ${({normal: {fontSize}}) => fontSize || '24px'};
  font-family: ${({normal: {fontFamily}}) => fontFamily || 'inherit'};
  font-weight: ${({normal: {fontWeight}}) => fontWeight || 'bold'};
  padding: 20px;
  border: ${({normal: {border}}) => border || 'solid 2px #fff'};
`
export default Result
