import * as React from 'react'

import QuizTitle from './components/QuizTitle'
import QuizHeader from './components/QuizHeader'
import { Pagination, PaginationItem } from './components/Pagination'
import QuizWrap from './components/QuizWrap'
import Result from './components/Result'
import ImageBlock from './components/ImageBlock'
import { withTranslations } from './Translation'

const transformColor = (color) => color === '#000' ? '0,0,0' : '255,255,255'

const ResultPage = ({
  title,
  finalTeaserImage,
  countRightAnswer,
  questionItems, result,
  handleOnPageStart,
  T, handleOnPage,
  finalTeaserBackground,
  options, block, header, button,
  styles
}) => {
  const color = transformColor(options.resultBackground || (finalTeaserBackground && finalTeaserBackground.color));
  questionItems = options.questions || questionItems;
  block = styles.block || block;
  header = styles.header || header;
  button = styles.button || button;

  return (
    <>
      <QuizWrap
        border={block.border}
        borderRadius={block.borderRadius}
      >
        <QuizTitle
          fontSize={header.fontSize}
          fontFamily={header.fontFamily}
          fontWeight={header.fontWeight}
          lineHeight={header.lineHeight}
          color={header.color}
        >
          {options.title || title}
        </QuizTitle>
        <QuizHeader background={`rgba(${color}, ${options.resultOpacity || (finalTeaserBackground ? finalTeaserBackground.opacity : 0)})`}>
          <ImageBlock>
            <img src={options.resultImage || finalTeaserImage} alt="" />
          </ImageBlock>
          <Result normal={button.normal}>
            {T.result(countRightAnswer, questionItems.length)}
          </Result>
          <div className="teaser-image-fade"/>
        </QuizHeader>
      </QuizWrap>
      <Pagination>
        <PaginationItem onClick={handleOnPageStart}>start</PaginationItem>
          {
            questionItems.map((item, index) => (
              <PaginationItem
                key={item.id}
                onClick={() => handleOnPage(index + 1)}
              >
                {index + 1}
              </PaginationItem>
            ))
          }
        <PaginationItem border={result && 'solid 2px blue'}>results</PaginationItem>
      </Pagination>
    </>
  )
}

export default withTranslations(ResultPage)
