import { IRegexField } from '../Field'

export interface IFormParams {
  formProps: {
    ref: any
    onSubmit: (e: Event) => void
    onKeyDown: (e: KeyboardEvent) => void
  }
  disabled: boolean
  dirty: boolean
  submitting: boolean
  getValues: () => any
}

export const validate = (
  fieldProps: IRegexField,
  formParams: IFormParams,
  getSubmitted: () => boolean
) => (value: string) => {
  const match = value.match(fieldProps.regex as RegExp)
  const values = formParams.getValues()

  const submitted = getSubmitted();

  if(!fieldProps.validOnType && !submitted){
    return undefined;
  }
  switch (fieldProps.validationType) {
    case 'password': {
      if (values.password.length < 1) return 'IS_EMPTY';
      if (values.password.length > 50) return 'MAX_CHARACTERS'
      if (values.password === values.email) return 'PASSWORD_AS_EMAIL'
      const passwordToShort = match ? undefined : 'PASSWORD_ERROR';
      return passwordToShort;
    }
    case 'email': {
      if (values.email.length < 1) return 'IS_EMPTY';
      if (values.email.length > 255) return 'MAX_CHARACTERS'
      const loginTooShort = match && values.email.length > 0 ? undefined : 'LOGIN_TOO_SHORT';
      return loginTooShort;
    }
    case 'password_confirmation': {
      if (values.password_confirmation < 1) return 'IS_EMPTY';
      const isValid = match && values.password_confirmation === values.password;
      return isValid ? undefined : 'PASSWORD_NOT_SAME';
    }
    case 'url': {
      if (values.siteUrl < 1) return 'IS_EMPTY';
      const isValid = match ? undefined : 'SITE_URL_ERROR';
      return isValid;
    }
    case 'name': {
      if (values.name < 1) return 'IS_EMPTY';
      const isValid = values.name.length >= 3 ? undefined : 'NAME_TOO_SHORT';
      return isValid;
    }
  }
}
