import styled from 'styled-components'

export const ToggleInput = styled.input`
  position: absolute;
  z-index: -1;
  opacity: 0;
  margin: 10px 0 0 20px;
  outline: none;
  &:disabled + label {
    cursor: not-allowed;
  }
  &:checked + label::before {
    background: #689F4B;
  }
  &:checked + label::after {
    left: 25px;
  }
`
export const ToggleLabel = styled.label`
  position: relative;
  padding: 0 0 0 46px;
  cursor: pointer;

  &::after {
    content: '';
    position: absolute;
    top: -2px;
    left: 2px;
    width: 18px;
    height: 18px;
    border-radius: 10px;
    background: #FFF;
    box-shadow: 0 2px 5px rgba(0,0,0,.3);
    transition: .2s;
  }

  &::before {
    content: '';
    position: absolute;
    top: -4px;
    left: 0;
    width: 45px;
    height: 22px;
    border-radius: 13px;
    background: #CDD1DA;
    box-shadow: inset 0 2px 3px rgba(0,0,0,.2);
    transition: .2s;
  }
`