import React, { useState } from 'react'
import styled from 'styled-components'
import Spinner from '@atlaskit/spinner'

import Container from '../components/WidthContainer'
import SpinnerContainer from '../components/SpinnerContainer'
import AppMain from '../components/AppMain'
import { IRegexField } from '../components/Field'
import Form from '../components/Form'
import { sendURL, getData } from '../api'
import withAuth from '../components/Auth/component/WithAuth'
import { configureAll, makePropsStyles } from '../Configurations'

const Text = styled.div`
  text-align: center;
  font-weight: bold;
  font-size: 16px;
  & p {
    margin: 0;
  }
`
const FormWrapp = styled.div`
  max-width: 450px;
  margin: 10% auto;
`

const SiteUrlFields: IRegexField[] = [
  {
    fieldName: 'url',
    placeholder: 'Enter website address',
    textFieldProps: {
      type: 'text',
    },
    fieldProps: {
      isRequired: true,
    },
    validationType: 'url',
    validOnType: true,
    regex: /^http(s)?:\/\/(www\.)?[a-z0-9]+([\-\.]{1}[a-z0-9]+)*\.[a-z]{2,5}(:[0-9]{1,5})?(\/.*)?$/
  }
]

const ParserPage = ({user, history}) => {
  const [submiting, setSubmiting] = useState(false);
  const [error, setError] = useState();

  const userName = user ? user : 'guest';

  return (
    <AppMain>
      <Container>
        <FormWrapp>
          <Text>
            <p>Hello {userName}! Start creating your interactive right now.</p>
            <p>First, let's look at the design of your website</p>
            <p>Please enter the address</p>
          </Text>
          <Form
            formName="START"
            fields={SiteUrlFields}
            parsError={error}
            onSubmit={(formData: any) => {
              setSubmiting(true);
              sendURL(formData)
              .then(response => {
                if (response.id) {
                  getData(response.id)
                  .then((response: any) => {
                    if (response) {
                      setSubmiting(false);

                      const result = configureAll(response);
                      const props = makePropsStyles(result.styles,result.colors);
                      
                      sessionStorage.setItem('parsData', JSON.stringify(response));
                      sessionStorage.setItem('configured colors', JSON.stringify(result.colors));
                      sessionStorage.setItem('configured styles', JSON.stringify(result.styles));
                      sessionStorage.setItem('props', JSON.stringify(props));

                      history.replace('/editor');
                    }
                  })
                }
              })
              .catch(error => {
                setError(error.message);
              })}
            }
          />
        </FormWrapp>
        {
          submiting &&
          <SpinnerContainer>
            <Spinner size="large" />
            <p>Please, wait</p>
          </SpinnerContainer>
        }
      </Container>
    </AppMain>
  )
}

export default withAuth(ParserPage)