import React from 'react'
import styled from 'styled-components'
import { Route, Switch, BrowserRouter } from 'react-router-dom'

import { LoginPage, ParserPage, ParsedPages, SignUpPage, EditorPage} from './pages/index.ts'
import Menu from './components/Menu'
import Nav from './components/Nav'
import Container from './components/WidthContainer'
import Logo from './components/Logo'

const Header = styled.header`
  background: #fff;
  position: fixed;
  width: 100%;
  z-index: 100;
`
const HeaderContainer = styled(Container)`
  display: flex;
  justify-content: space-between;
`
const Wrapp = styled.div`
  display: flex;
  align-items: center;
`

const App = () => {
  return (
    <BrowserRouter>
      <Header>
        <HeaderContainer>
          <Wrapp>
            <Logo />
            <Nav />
          </Wrapp>
          <Menu />
        </HeaderContainer>
      </Header>
      <Switch>
        <Route path="/" exact component={ParserPage} />
        <Route path="/login" component={LoginPage} />
        <Route path="/registration" component={SignUpPage} />
        <Route path="/editor" component={EditorPage} />
        <Route path="/parsed-pages" component={ParsedPages} />
      </Switch>
    </BrowserRouter>
  )
}

export default App
