//this function is for configuring default styles or mock api (json)
// this prepares arrays for buttons
//@ts-ignore
var colorsObj={};
var stylesObj={};
function makeArray(obj,abs) {
  if((abs==='button')&&(obj[abs]["normal"]["fontSize"].length>0)){
    stylesObj[abs] = {};
    colorsObj[abs] = {};
    for(var key in obj[abs]){
      colorsObj[abs][key] = [];
      stylesObj[abs][key]=[]
    }
  }else if((abs==='button')&&(obj[abs]["normal"]["fontSize"].length===0)){
    stylesObj[abs] = {};
    colorsObj[abs] = {};
    for(var key in obj["a"]){
      colorsObj[abs][key] = [];
      stylesObj[abs][key]=[]
    }
  }else if((abs!=='a')&&(abs!=='button')){
    stylesObj[abs] = [];
    colorsObj[abs] = [];
  }
}
function makeStyles (obj) {
  Object.keys(obj).map(abstr => (
    makeArray(obj,abstr)
  ));
}
var style;
function StyleButton(obj,abs){
  for(var type in obj[abs]){
    for(style in obj[abs][type]){

      if((style!=='background')&&(style!=='backgroundColor')){
        var s ={}
        obj[abs][type][style].forEach((value, i) => {
              
          for(style in obj[abs][type]){
            if((i<obj[abs][type][style].length)&&(style!=='background')&&(style!=='backgroundColor')){
              s[style] = obj[abs][type][style][i];
            }
          }
          
          if(stylesObj["button"][type].length<obj[abs][type][style].length){
              stylesObj["button"][type].push(s);  
              }
          s=null;  
          s={};   
        });
      }
      else if((style ==='background')||(style === "backgroundColor")){
        obj[abs][type][style].forEach((value, i) =>{
          if (value!=='transparent'){
            for(style in obj[abs][type]){
              if((style==='background')||(style==='backgroundColor')){
                if((i<obj[abs][type][style].length)&&(colorsObj["button"][type].length<obj[abs][type][style].length)){
                  colorsObj["button"][type].push(value);
                }
              }
            } 
          }
        });
      }
  }}
}

//main function of configuration
function StyleConf(obj,abs) {
  if(((abs==='button')&&(obj['button']['normal']['fontSize'].length>0))||((abs==='a')&&(obj['button']['normal']['fontSize'].length===0))){
      StyleButton(obj,abs);
  }
  else if((abs!=='a')&&(abs!=="button")){
    for(var style in obj[abs]) {
      if((style!=='background')&&(style!=='backgroundColor')&&(style!=='color')) {
      var s ={};
      obj[abs][style].forEach((value, i) => {
        
        for(style in obj[abs]){
          if((abs==='block')&&(stylesObj[abs].length===0)&&(obj[abs]["border"][0]!=="none")){
              stylesObj[abs].push({'border':'none','borderRadius':'none'})
          }
          
          if((style!=='background')&&(style!=='backgroundColor')&&(style!=='color')&&(style!=='Color')){
            if(i<obj[abs][style].length){
              s[style] = obj[abs][style][i];
            }else if((style==='textDecoration')&&(obj[abs]["textDecoration"][i]==="undefined")&&(i<obj[abs]["fontFamily"].length)){
              s[style] = 'none';
            }
          }
        }

        if(((stylesObj[abs].length<obj[abs][style].length)&&(s))||((style==="textDecoration")&&(stylesObj[abs].length<obj[abs]['fontFamily'].length)&&(s))) {
          stylesObj[abs].push(s);
          s=null;
          s={};
        } 
    });
          
      
      
      }
    else if((style ==='background')||(style === "backgroundColor")||(style==='color')||(style==='Color')){
      obj[abs][style].forEach((value, i) => {
        if((i<obj[abs][style].length)&&(colorsObj[abs].length<obj[abs][style].length)&&(abs!=='block')){
          colorsObj[abs].push(value);
        }
    }); 
        
        s=null; 
    }
  }}
}

// configurating all styles
export function configureAll(object){
makeStyles(object);
Object.keys(object).map(abs => (
    StyleConf(object,abs)))
return { colors: colorsObj, styles: stylesObj}
}

//prepare props
export function makePropsStyles(objStyles,objColors){
var StylesParserProps = [];
var abs;
for(abs in objStyles){
    var temp={};
    switch(abs){
        case "button":
            temp.title = "Button:"
        break;
        case "header":
            temp.title = "Title:"
        break;
        case "block": 
            temp.title = "Quiz Block:"
        break;
        case "paragraph":
            temp.title = "Questions & Answers:"
        break;
    }
    temp.color = [];
    if((abs!=='base')&&(abs!=='a')){
        if(abs!=="button"){
        objColors[abs].forEach((val, i) => {
            var t = {
                label: i+1,
                value: val
            }
            if((abs!=='block')&&(abs!=='a')){
                temp.color.push(t);
            }
            t=0;
        })
    }else if(abs!=='a'){
        objColors[abs]['normal'].forEach((val, i) => {
            var t = {
                label: i+1,
                value: val
            }
            if(abs!=='block'){
                temp.color.push(t);
            }
            t=0;
        })
    }
    temp.style=[];
    if((abs!=="button")&&(abs!=='a')){
        objStyles[abs].forEach((val,i) => {
            var h = {
                label: "Style "+(i+1),
                value: i+1
            }
            temp.style.push(h);
            h=0;
        })
    }else if(abs!=='a'){
        objStyles[abs]['normal'].forEach((val,i) => {
            var h = {
                label: "Style "+(i+1),
                value: i+1
            }
            temp.style.push(h);
            h=0;
        })
    }
    StylesParserProps.push(temp)
}}
return StylesParserProps
}