import * as React from 'react'
import EditorCloseIcon from '@atlaskit/icon/glyph/editor/close'
import ColorPicker from '@atlaskit/color-picker'
import FieldRange from '@atlaskit/field-range'
import { ErrorMessage } from '@atlaskit/form'

import BasicPartContainer from './components/BasicPartContainer'
import BasicField from './components/BasicField'
import InputGroup from './components/InputGroup'
import RemoveIcon from './components/RemoveIcon'
import ContentControl from './components/ControlContainer'
import RangeContainer from './components/RangeContainer'
import ControlLabel from './components/RangeLabel'
import ColorPickerContainer from './components/ColorPickerContainer'

const FinalTeaser = ({
  finalTeaserImage,
  simplePalette,
  finalTeaserBackground,
  setOpacityFinalBackgroundTeaser,
  changeFinalBackgroundTeaser,
  onChangeHandle,
  removeFinalTeaserImage,
  finalTeaserImageValid
}) => (
  <BasicPartContainer>
        
    <InputGroup>     
      <label htmlFor="finalTeaserImage">Final Teaser:</label>
      <BasicField
        name="finalTeaserImage"
        value={finalTeaserImage}
        placeholder='Enter URL'
        onChange={onChangeHandle}
        isInvalid ={!finalTeaserImageValid}
      />
      {!finalTeaserImageValid && <ErrorMessage>Enter correct image URL</ErrorMessage>}
      {
        finalTeaserImage &&
          <RemoveIcon onClick={removeFinalTeaserImage}>
            <EditorCloseIcon label='' />
          </RemoveIcon>
      }
    </InputGroup>
    {
      finalTeaserImage && finalTeaserImageValid &&
      <ContentControl>
        <ColorPickerContainer>
        <ControlLabel>Overlay Color:</ControlLabel>
          <ColorPicker
            label="Change color"
            palette={simplePalette}
            checkMarkColor='#24B124'
            selectedColor={finalTeaserBackground.color}
            onChange={(newColor) => changeFinalBackgroundTeaser(newColor)}
          />
        </ColorPickerContainer>
        
        <RangeContainer>
          <ControlLabel>Overlay Opacity:</ControlLabel>
          <FieldRange
            value={finalTeaserBackground.opacity}
            min={0}
            max={1}
            step={0.1}
            onChange={(value) => setOpacityFinalBackgroundTeaser(value)}
          />
        </RangeContainer>
      </ContentControl>
    }
  </BasicPartContainer>
)

export default FinalTeaser