import * as React from 'react'
import EditorCloseIcon from '@atlaskit/icon/glyph/editor/close'
import { ErrorMessage } from '@atlaskit/form'

import BasicPartContainer from './components/BasicPartContainer'
import InputGroup from './components/InputGroup'
import BasicField from './components/BasicField'
import RemoveIcon, { RemoveIconAnswer } from './components/RemoveIcon'
import QuestionNumber from './components/QuestionNumber'
import AnswerContainer from './components/AnswerContainer'
import AnswerNumber from './components/AnswerNumber'
import ToggleConteainer from './components/ToggleContainer'
import { ToggleInput, ToggleLabel} from './components/Toggler'
import AnswersHeader from './components/AnswersHeader'
import RemoveQuestionButton from './components/RemoveQuestionButton'

const QuestionPart = ({
  questionItem,
  questionItemsLength,
  itemIndex,
  changeQuestion,
  removeQuestion,
  changeImage,
  removeImage,
  changeAnswers,
  removeAnswers,
  selectRightAnswer,
  removeQuestionItem,
}) => (
  <BasicPartContainer>
    {
      questionItemsLength > 1 &&
      <RemoveQuestionButton onClick={() => removeQuestionItem(questionItem.id)}>
        <EditorCloseIcon label='' />
      </RemoveQuestionButton>
    }
    <QuestionNumber>{itemIndex + 1}</QuestionNumber>
    <InputGroup>
      <label htmlFor="question">Question:</label>
      <BasicField
        name="question"
        value={questionItem.question}
        onChange={(event) => changeQuestion(event, questionItem.id)}
      />
      {
        questionItem.question &&
        <RemoveIcon  onClick={() => removeQuestion(questionItem.id)}>
          <EditorCloseIcon label='' />
        </RemoveIcon>
      }
    </InputGroup>
    <AnswersHeader>
      <span>Answers:</span>
      <span>Correct:</span>
    </AnswersHeader>
    {
      questionItem.answers.map((answer) => (
        <AnswerContainer key={`answer${questionItem.id}${answer.id}`}>
          <AnswerNumber>{`${itemIndex + 1}. ${answer.id}.`}</AnswerNumber>
          <InputGroup>
            <BasicField
              name="answer"
              value={answer.value}
              onChange={(event) => changeAnswers(event, questionItem.id, answer.id)}
              border={answer.id === questionItem.indexRightAnswer && answer.value ? 'solid 1px #9CCE64' : undefined}
            />
            {
              answer.value &&
              <RemoveIconAnswer  onClick={() => removeAnswers(questionItem.id, answer.id)}>
                <EditorCloseIcon label='' />
              </RemoveIconAnswer>
            }
          </InputGroup>
          <ToggleConteainer>
            <ToggleInput
              type="radio"
              id={`toggler${questionItem.id}${answer.id}`}
              name={`toggler${questionItem.id}`}
              checked={answer.id === questionItem.indexRightAnswer && answer.value}
              disabled={!answer.value}
              onChange={() => selectRightAnswer(questionItem.id, answer.id)}
            />            
            <ToggleLabel htmlFor={`toggler${questionItem.id}${answer.id}`} />
          </ToggleConteainer>
        </AnswerContainer>
      ))
    }

    <InputGroup>     
      <label htmlFor="image">Image:</label>
      <BasicField
        name="image"
        placeholder='Enter URL' 
        value={questionItem.image}
        onChange={(event) => changeImage(event, questionItem.id)}
        isInvalid = {!questionItem.imageValid}
      />
      {!questionItem.imageValid && <ErrorMessage>Enter correct image URL</ErrorMessage>}
      {
        questionItem.image &&
          <RemoveIcon onClick={() => removeImage(questionItem.id)}>
            <EditorCloseIcon label='' />
          </RemoveIcon>
      }
    </InputGroup>
  </BasicPartContainer>
)

export default QuestionPart