import styled from 'styled-components'
// import { withButtonStyles } from '../../withDefinedStyles'

export const ButtonNext = styled.div`
  width: 100%;
  text-align: center;
  padding-top: 40px;
`
const Next = styled.button`
  color: ${({normal: {color}}) => color || '#2a2a2a'};
  background: ${({normal: {background}}) => background || '#ddd'};
  border: ${({normal: {border}}) => border || '2px solid #ddd'};
  padding: 10px 40px;
  font-size: ${({normal: {fontSize}}) => fontSize || '16px'};
  font-family: ${({normal: {fontFamily}}) => fontFamily || "inherit"};
  font-weight: ${({normal: {fontWeight}}) => fontWeight || 'normal'};
  transition: .3s;
  cursor: pointer;
  &:hover {
    background: ${({hover: {background}}) => background || '#fff'};
    color: ${({hover: {color}}) => color || '#2a2a2a'};
    border: ${({hover: {border}}) => border || '2px solid #ddd'};
  }
  &:disabled {
    cursor: not-allowed;
  }
`

export default Next
