import * as React from 'react'
import EditorCloseIcon from '@atlaskit/icon/glyph/editor/close'
import ColorPicker from '@atlaskit/color-picker'
import FieldRange from '@atlaskit/field-range'
import { ErrorMessage } from '@atlaskit/form'

import { withTranslations } from '../../Translation'
import BasicPartContainer from './components/BasicPartContainer'
import RemoveIcon from './components/RemoveIcon'
import InputGroup from './components/InputGroup'
import BasicField from './components/BasicField'
import ContentControl from './components/ControlContainer'
import RangeContainer from './components/RangeContainer'
import ControlLabel from './components/RangeLabel'
import ColorPickerContainer from './components/ColorPickerContainer'

const BasicPart = ({
  T,
  title,
  teaserImage,
  onChangeHandle,
  removeTitle,
  removeTeaserImage,
  simplePalette,
  teaserBackground,
  changeBackgroundTeaser,
  setOpacityBackgroundTeaser,
  teaserImageValid
}) => (
  <BasicPartContainer>
    <InputGroup>
      <label htmlFor="title">{T.basicPartTitle}</label>
      <BasicField
        name="title"
        value={title}
        onChange={onChangeHandle}
      />
      {
        title &&
        <RemoveIcon  onClick={removeTitle}>
          <EditorCloseIcon label='' />
        </RemoveIcon>
      }
    </InputGroup>
        
    <InputGroup>     
      <label htmlFor="teaserImage">{T.basicPartTeaser}</label>
      <BasicField
        name="teaserImage"
        value={teaserImage}
        placeholder={T.basicPartTeaserPlaceholder}
        onChange={onChangeHandle}
        isInvalid ={!teaserImageValid}
      />
      {!teaserImageValid && <ErrorMessage>Enter correct image URL</ErrorMessage>}
      {
        teaserImage &&
          <RemoveIcon onClick={removeTeaserImage}>
            <EditorCloseIcon label='' />
          </RemoveIcon>
      }
    </InputGroup>
    {
      teaserImage && teaserImageValid &&
      <ContentControl>
        <ColorPickerContainer>
          <ControlLabel>Overlay Color:</ControlLabel>
          <ColorPicker
            label="Change color"
            palette={simplePalette}
            checkMarkColor='#24B124'
            selectedColor={teaserBackground.color}
            onChange={(newColor) => {changeBackgroundTeaser(newColor)}}
          />
        </ColorPickerContainer>
        
        <RangeContainer>
          <ControlLabel>Overlay Opacity:</ControlLabel>
          <FieldRange
            value={teaserBackground.opacity}
            min={0}
            max={1}
            step={0.1}
            onChange={(value) => setOpacityBackgroundTeaser(value)}
          />
        </RangeContainer>
      </ContentControl>
    }
  </BasicPartContainer>
)

export default withTranslations(BasicPart)