import * as React from 'react'
import Button from '@atlaskit/button'
import BaseForm, { FormFooter, FormHeader, FormSection } from '@atlaskit/form'
import SectionMessage from '@atlaskit/section-message'

import { IWithTranslations, withTranslations } from '../../Translation'
import Field, { IRegexField } from '../Field'
import { IFormParams, validate } from './validate'

export interface IField extends IRegexField {}

export interface ILoginForm extends IWithTranslations {
  fields: IField[]
  formName: string
  onSubmit: any
  authError: string
  parsError: string
  registerError: string
}

export interface IFormState {
  submitted?: boolean,
}

class Form extends React.Component<ILoginForm, IFormState> {
  state = {
    submitted: false,
  }

  render () {
    const {T, fields, formName, onSubmit, authError, parsError, registerError} = this.props;
    const { submitted } = this.state;
    const getSubmitted = () => submitted;

    return (
      <BaseForm
        onSubmit={onSubmit}
      >
        {(formParams: IFormParams) => (
          // @ts-ignore
          <form {...formParams.formProps}>
          <FormHeader 
            title={formName==='SIGN IN' && T && T.signIn.toUpperCase() || 
                  formName==='SIGN UP' && T && T.signUp.toUpperCase()}
          />
            {
              authError === '422' &&
                <SectionMessage appearance="error" title={T && T.errorTitle} >
                  <p>{T && T.errors.INCORRECT}</p>
                </SectionMessage>
            }
            {
              parsError &&
                <SectionMessage appearance="error" title={T && T.errorTitle} >
                  <p>{T && T.errors.PARS_ERROR}</p>
                </SectionMessage>
            }
            {
              registerError &&
              <SectionMessage appearance="error" title={T && T.errorTitle} >
                <p>{registerError}</p>
              </SectionMessage>
            }
            <FormSection>
              {
                fields.map(fieldProps =>
                  <Field
                    disabled={formParams.submitting}
                    key={fieldProps.fieldName}
                    validate={validate(fieldProps, formParams, getSubmitted)}
                    {...{...fieldProps, ...this.state}}
                  />
                )
              }
            </FormSection>
            <FormFooter>
              <Button
                shouldFitContainer
                type="submit"
                isLoading={formName !== 'START' && formParams.submitting}
                appearance="primary"
              >
                {formName === 'SIGN IN' && T && T.signIn}
                {formName === 'SIGN UP' && T && T.signUp}
                {formName === 'START' && T && T.start}
              </Button>
            </FormFooter>
          </form>
        )}
      </BaseForm>
    )
  }
}

export default withTranslations(Form)
