import styled from 'styled-components'
import { Link } from 'react-router-dom'

const NavLink = styled(Link)`
  color: #070707;
  font-size: 18px;
  position: relative;
  margin-right: 15px;
  
  &:last-child {
    margin-right: 0;
  }

  &::after {
    content: '';
    display: block;
    width: 100%;
    height: 2px;
    background: #1A3E73;
    position: absolute;
    top: 25px;
    left: 0;
    transform: scale(0);
    opacity: 0;
    transition: .2s
  } 

  &:hover {
    color: #1A3E73;
    text-decoration: none;

    &::after {
      transform: scale(1);
      opacity: 1;
    }
  }

  &:active, &:focus {
    outline: none;
  }
`
export default NavLink