import styled from 'styled-components'

const AddQuestionButton = styled.button`
  margin: 0 auto;
  display: block;
  border: solid 2px #6a90e8;
  border-radius: 3px;
  background: #fff;
  color: #6a90e8;
  padding: 5px 15px 5px 35px;
  font-weight: bold;
  cursor: pointer;
  outline: none;
  position: relative;
  &:hover {
    color: #244AC2;
    > span {
      color: #244AC2;
    }
  }
  > span {
    border-right: solid 1px #6a90e8;
    padding: 0 2px;
    position: absolute;
    left: 0;
    top: 0;
  }
`
export default AddQuestionButton