export const getCode = (state) => {
  return `
    <div id="funlib"></div>
    <script>
      var funlib = {
        isEmbed : true,
        options: {
          title: '${state.title}',
          startBackground: '${state.teaserBackground.color}',
          startOpacity: ${state.teaserBackground.opacity},
          startImage: '${state.teaserImage}',
          resultBackground: '${state.finalTeaserBackground.color}',
          resultOpacity: ${state.finalTeaserBackground.opacity},
          resultImage: '${state.finalTeaserImage}',
          questions: ${JSON.stringify(state.questionItems)}
        },
        base: {
          fontSize: '${state.options.base.fontSize}',
          backgroundColor: '${state.options.base.backgroundColor}',
          border: '${state.options.base.border}',
          borderRadius: '${state.options.base.borderRadius}',
        },
        block: {
          background: '${state.options.block.background}',
          border: '${state.options.block.border}',
          borderRadius: '${state.options.block.borderRadius}',
        },
        header: {
          fontFamily: '${state.options.header.fontFamily}',
          fontSize: '${state.options.header.fontSize}',
          color: '${state.options.header.color}',
          lineHeight: '${state.options.header.lineHeight}',
          textDecoration: '${state.options.header.textDecoration}',
          fontWeight: '${state.options.header.fontWeight}',
        },
        button: {
          normal: {
            color: '${state.options.button.normal.color}',
            background: '${state.options.button.normal.background}',
            border: '${state.options.button.normal.border}',
            fontSize: '${state.options.button.normal.fontSize}',
            fontFamily: '${state.options.button.normal.fontFamily}',
            fontWeight: '${state.options.button.normal.fontWeight}',
          },
          hover: {
            background: '${state.options.button.hover.background}',
            color: '${state.options.button.hover.color}',
            border: '${state.options.button.hover.border}',
          },
        },
        paragraph: {
          fontFamily: '${state.options.paragraph.fontFamily}',
          fontSize: '${state.options.paragraph.fontSize}',
          color: '${state.options.paragraph.color}',
        },
      }
    </script>

    <script src="${window.location.origin}/funlib/shared/2.647966a5.chunk.js"></script>
    <script src="${window.location.origin}/funlib/shared/main.cb066904.chunk.js"></script>
    <script src="${window.location.origin}/funlib/shared/runtime~main.c5541365.js"></script>
  `
}