import React, { useState } from 'react'

import { IRegexField } from '../components/Field'
import AppMain from '../components/AppMain'
import Container from '../components/WidthContainer'
import Form from '../components/Form'
import FormWrapp from '../components/FormWrapp'
import AuthService from '../components/Auth/AuthService'

const SignInFields: IRegexField[] = [
  {
    fieldName: 'email',
    // label: 'Email',
    placeholder: 'username@your.mail',
    textFieldProps: {
      type: 'text',
    },
    fieldProps: {
      isRequired: true,
    },
    validationType: 'email',
    // validMessage: 'Email is valid',
    validOnType: true,
    regex: /^(([^<>()\\[\]\\.,;:\s@‚Äú]+(\.[^<>()\\[\]\\.,;:\s@‚Äú]+)*)|(‚Äú.+‚Äú))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
  },
  {
    fieldName: 'password',
    // label: 'Password',
    placeholder: 'password',
    textFieldProps: {
      type: 'password',
    },
    fieldProps: {
      isRequired: true,
    },
    validOnType: true,
    validationType: 'password',
  }
]

const LoginPage = ({history}) => {
  const Auth = new AuthService();
  const [error, setError] = useState();
 
  return (
    <AppMain>
      <Container>
        <FormWrapp>
          <Form
            formName="SIGN IN"
            fields={SignInFields}
            authError={error}
            onSubmit={(formData: any) => Auth.logIn(formData)
              .then(response => {
                if(response) {
                  history.replace('/');
                }
              })
              .catch(error => {
                setError(error.message);
              })}
          />
        </FormWrapp>
      </Container>
    </AppMain>
  )
}
  
export default LoginPage