import React from 'react'
import styled from 'styled-components'
import { Link, withRouter } from 'react-router-dom'

import AuthService from '../Auth/AuthService';

const LoginBtn = styled(Link)`
  padding: 8px 30px;
  font-size: 16px;
  display: block;
  background: #0052CC;
  border-radius: 3px;
  color: #fff;
  transition: .2s;
  &:hover {
    text-decoration: none;
    color: #fff;
    background: #0065FF;
  }
  &:active, &:focus {
    outline: none;
  }
`
const SignUpBtn = styled(LoginBtn)`
  background: transparent;
  border: none;
  color: #1A3E73;
  padding: 0;
  margin-right: 15px;
  &:hover {
    text-decoration: underline;
    background: transparent;
    color: #1A3E73;
  }
`
const Linkwrapp = styled.div`
  display: flex;
  align-items: center;
`

const Menu = () => {
  const Auth = new AuthService();

  const handleLogOut = () => {
    Auth.logout();
  }

  return (
    <Linkwrapp>
      {
        Auth.loggedIn() ? (
          <LoginBtn to="/login" onClick={handleLogOut}>Sign out</LoginBtn>
        ) : (
          <>
            <SignUpBtn to="/registration">Sign Up</SignUpBtn>
            <LoginBtn to="/login">Sign In</LoginBtn>
          </>
        )
      }
    </Linkwrapp>
  )
}

export default withRouter(Menu)