import * as React from 'react'
import styled from 'styled-components'

import ListStyles from '../ListStyles'

const AllStylesContainer = styled.div`
  margin: 50px;
`

const AllStyles = ({styles, setColor, setStyle}) => {
  let temp = 0;

  const Items = styles.map((item) => 
    <ListStyles 
      title={item.title} 
      colors={item.color}
      styles={item.style}
      key={temp+=1}
      setColor={setColor} 
      setStyle={setStyle}
    />
  )

  return (
    <AllStylesContainer>
      {Items}
    </AllStylesContainer>
  )
}

export default AllStyles