import * as React from 'react'
import AddCircleIcon from '@atlaskit/icon/glyph/add-circle'
import EditorInfoIcon from '@atlaskit/icon/glyph/editor/info';
import { CSSTransition, TransitionGroup } from 'react-transition-group';

import withAuth from '../components/Auth/component/WithAuth'
import AppMain from '../components/AppMain'
import Container from '../components/WidthContainer'
import { Content, ContentRight } from '../components/Content'
import EditorHeading from '../components/Editor/components/EditorHeading'
import TextMessage from '../components/Editor/components/textMessage'
import Slider from '../components/Slider'
import BasicPart from '../components/Editor/BasicPart'
import QuestionPart from '../components/Editor/QuestionPart'
import FinalTeaser from '../components/Editor/FinalTeaser'
import Quiz from '@components/quiz'
import AddQuestionButton from '../components/Editor/components/AddQuestionButton'
import CodeModal from '../components/CodeModal'
import AllStyles from '../components/StylesSelect/AllStyles'
import { getCode } from '../Configurations/code'


class EditorPage extends React.Component {

  state = {
    title: '',
    teaserImage: '',
    finalTeaserImage: '',
    teaserImageValid: true,
    finalTeaserImageValid: true,
    teaserBackground: {
      color: '#000',
      opacity: 0.5
    },
    finalTeaserBackground: {
      color: '#000',
      opacity: 0.5
    },
    questionItems: [
      {
        id: 1,
        image:'',
        question: '',
        indexRightAnswer: 0,
        itemValid: false,
        imageValid: true,
        answers: [
          {id: 1, value:''},
          {id: 2, value:''},
          {id: 3, value:''}
        ]
      }
    ],
    codeModalDisabled: true,
    styles: [],
    configuredStyles: {},
    configuredColors: {},
    options: {
      base: {
        fontSize: '',
        backgroundColor: '',
        border: '',
        borderRadius: '',
      },
      block: {
        background: '',
        border: '',
        borderRadius: '',
      },
      header: {
        fontFamily: '',
        fontSize: '',
        color: '',
        lineHeight: '',
        textDecoration: '',
        fontWeight: '',
      },
      button: {
        normal: {
          color: '',
          background: '',
          border: '',
          fontSize: '',
          fontFamily: '',
          fontWeight: '',
        },
        hover: {
          background: '',
          color: '',
          border: '',
        },
      },
      paragraph: {
        fontFamily: '',
        fontSize: '',
        color: '',
      }
    }
  }

  componentWillMount = () => {
    //@ts-ignore
    const styles = JSON.parse(sessionStorage.getItem('props'));
    //@ts-ignore
    const configuredStyles = JSON.parse(sessionStorage.getItem('configured styles'));
    //@ts-ignore
    const configuredColors = JSON.parse(sessionStorage.getItem('configured colors'));

    if (styles && configuredStyles && configuredColors) {
      this.setParsData(styles, configuredStyles, configuredColors);
    }
  }

  setParsData = (styles:[], configuredStyles:{}, configuredColors:{}) => {
    const options = {...this.state.options};
    const confStyles:any = configuredStyles;
    const confColors:any = configuredColors;

    options.block.border = confStyles.block[0] && confStyles.block[0].border;
    options.block.borderRadius = confStyles.block[0] && confStyles.block[0].borderRadius;
    options.header.fontFamily = confStyles.header[0] && confStyles.header[0].fontFamily;
    options.header.fontSize = confStyles.header[0] && confStyles.header[0].fontSize;
    options.header.fontWeight = confStyles.header[0] && confStyles.header[0].fontWeight;
    options.header.lineHeight = confStyles.header[0] && confStyles.header[0].lineHeight;
    options.header.textDecoration = confStyles.header[0] && confStyles.header[0].textDecoration;
    options.header.color = confColors.header[0] && confColors.header[0];
    options.button.normal.border = confStyles.button.normal[0] && confStyles.button.normal[0].border;
    options.button.normal.color = confStyles.button.normal[0] && confStyles.button.normal[0].color;
    options.button.normal.fontFamily = confStyles.button.normal[0] && confStyles.button.normal[0].fontFamily;
    options.button.normal.fontSize = confStyles.button.normal[0] && confStyles.button.normal[0].fontSize;
    options.button.normal.fontWeight = confStyles.button.normal[0] && confStyles.button.normal[0].fontWeight;
    options.button.normal.background = confColors.button.normal[0] && confColors.button.normal[0];
    options.button.hover.border = confStyles.button.hover[0] && confStyles.button.hover[0].border;
    options.button.hover.color = confStyles.button.hover[0] && confStyles.button.hover[0].color;
    options.button.hover.background = confColors.button.hover[0] && confColors.button.hover[0];
    options.paragraph.fontFamily = confStyles.paragraph[0] && confStyles.paragraph[0].fontFamily;
    options.paragraph.fontSize = confStyles.paragraph[0] && confStyles.paragraph[0].fontSize;
    options.paragraph.color = confStyles.paragraph[0] && confColors.paragraph[0];

    this.setState({
      styles,
      options,
      configuredStyles: confStyles,
      configuredColors: confColors
    });
  }

  onChangeHandle = (event:any) => {
    const name = event.target.name;
    const value = event.target.value;

    if (name === 'teaserImage') {
      this.setState({
        [name]: value,
        teaserImageValid: this.validateImage(value)
      });
    }
    else if (name === 'finalTeaserImage') {
      this.setState({
        [name]: value,
        finalTeaserImageValid: this.validateImage(value)
      });
    }
    else this.setState({[name]: value});
  }

  removeTitle = () => {
    this.setState({title: ''});
  }

  removeTeaserImage = () => {
    this.setState({
      teaserImage: '',
      teaserImageValid: true
    });
  }

  removeFinalTeaserImage = () => {
    this.setState({
      finalTeaserImage: '',
      finalTeaserImageValid: true
    });
  }

  changeQuestion = (event:any, id:number) => {
    const questionItems = [...this.state.questionItems];
    const questionItem = questionItems.find(item => item.id === id);

    if (questionItem) {
      questionItem.question = event.target.value;
      questionItem.itemValid = this.validateQuestionItem(id)
    }
      
    this.setState({questionItems}, this.activateCodeModal);
  }

  changeBackgroundTeaser = (color:string) => {
    const teaserBackground = {...this.state.teaserBackground};
    teaserBackground.color = color;

    this.setState({teaserBackground});
  }

  changeFinalBackgroundTeaser = (color:string) => {
    const finalTeaserBackground = {...this.state.finalTeaserBackground};
    finalTeaserBackground.color = color;

    this.setState({finalTeaserBackground});
  }

  setOpacityBackgroundTeaser = (opacity:number) => {
    const teaserBackground = {...this.state.teaserBackground};
    teaserBackground.opacity = opacity;

    this.setState({teaserBackground});
  }

  setOpacityFinalBackgroundTeaser = (opacity:number) => {
    const finalTeaserBackground = {...this.state.finalTeaserBackground};
    finalTeaserBackground.opacity = opacity;

    this.setState({finalTeaserBackground});
  }

  removeQuestion = (id:number) => {
    const questionItems = [...this.state.questionItems];
    const questionItem = questionItems.find(item => item.id === id);
    if (questionItem) {
      questionItem.question = '';
      questionItem.itemValid = this.validateQuestionItem(id)
    }
      
    this.setState({questionItems}, this.activateCodeModal);
  }

  changeImage = (event:any, id:number) => {
    const questionItems = [...this.state.questionItems];
    const questionItem = questionItems.find(item => item.id === id);
    
    if (questionItem) {
      questionItem.image = event.target.value;
      questionItem.imageValid = this.validateImage(event.target.value);
      questionItem.itemValid = this.validateQuestionItem(id);
    }
      
    this.setState({questionItems}, this.activateCodeModal);
  }

  removeImage = (id:number) => {
    const questionItems = [...this.state.questionItems];
    const questionItem = questionItems.find(item => item.id === id);
    if (questionItem) {
      questionItem.image = '';
      questionItem.imageValid = true;
      questionItem.itemValid = this.validateQuestionItem(id)
    }
      
    this.setState({questionItems}, this.activateCodeModal);
  }

  changeAnswers = (event:any, questionID:number, answerID:number) => { 
    const questionItems = [...this.state.questionItems];
    const questionItem = questionItems.find(item => item.id === questionID);

    if(questionItem) {
      const answers = [...questionItem.answers];
      const answer = answers.find(a => a.id === answerID);
      if (answer) {
        answer.value = event.target.value;
        questionItem.itemValid = this.validateQuestionItem(questionID)
      }
    }

    this.setState({questionItems}, this.activateCodeModal);
  }

  removeAnswers = (questionID:number, answerID:number) => {
    const questionItems = [...this.state.questionItems];
    const questionItem = questionItems.find(item => item.id === questionID);

    if(questionItem) {
      const answers = [...questionItem.answers];
      const answer = answers.find(a => a.id === answerID);
      if (answer) {
        answer.value = '';
        questionItem.indexRightAnswer = 0;
        questionItem.itemValid = this.validateQuestionItem(questionID)
      }
    }

    this.setState({questionItems}, this.activateCodeModal);
  }

  selectRightAnswer = (questionID:number, answerID:number) => {
    const questionItems = [...this.state.questionItems];
    const questionItem = questionItems.find(item => item.id === questionID);

    if (questionItem) {
      questionItem.indexRightAnswer = answerID;
      questionItem.itemValid = this.validateQuestionItem(questionID)
    }
      
    this.setState({questionItems}, this.activateCodeModal);
  }

  addNewQuestion = () => {
    const questionItems = [...this.state.questionItems];

    let newItemID = 0;
    if (questionItems.length > 0) {
      newItemID = questionItems[questionItems.length -1].id;
    }
    
    newItemID++;

    const newItems = {
      id: newItemID,
      image:'',
      question: '',
      indexRightAnswer: 0,
      itemValid: false,
      imageValid: true,
      answers: [
        {id: 1, value:''},
        {id: 2, value:''},
        {id: 3, value:''}
      ]
    }
    questionItems.push(newItems);

    this.setState({questionItems});
  }

  removeQuestionItem = (id:number) => {
    this.setState({
      questionItems: [...this.state.questionItems.filter(item => item.id !== id)]
    }, this.activateCodeModal);
  }

  validateImage = (imageUrl:string) => {
    const regexp = /^http(s)?:\/\/\S+(?:jpg|jpeg|png|gif)$/;
    
    if (imageUrl.match(regexp) || !imageUrl) {
      return true;
    }
    else return false;
  }

  validateQuestionItem = (id:number):any => {

    const checkAnswers = (answers:object) => {
      if (answers[0].value && answers[1].value || 
          answers[0].value && answers[2].value || 
          answers[1].value && answers[2].value) {
        return true;
      }
      else return false;
    }

    const questionItem = this.state.questionItems.find( item => item.id === id && item.question !== '' && item.indexRightAnswer > 0 && checkAnswers(item.answers) && item.imageValid);
    
    if (questionItem) {
      return true;
    } 
    else {
      return false;
    }
  }

  activateCodeModal = () => {
    const itemValid = this.state.questionItems.find(item => item.itemValid);
    
    if (itemValid) {
      this.setState({codeModalDisabled: false});
    }
    else {
      this.setState({codeModalDisabled: true});
    }
  }

  setColor = (newColor:string, title:string) => {
    const options = {...this.state.options};

    switch (title) {
      case 'Quiz Block:': {
        options.block.background = newColor;
        break;
      }
      case 'Title:': {
        options.header.color = newColor;
        break;
      }
      case 'Button:': {
        options.button.normal.background = newColor;
        break;
      }
      case 'Questions & Answers:': {
        options.paragraph.color = newColor;
        break;
      }
    }
    
    this.setState({options});
  }

  setStyle = (selectValue:any, title:string) => {
    const options = {...this.state.options};
    const exp = `${title}${selectValue.value}`;
    const configuredStyles:any = this.state.configuredStyles;

    switch (exp) {
      case 'Quiz Block:1': {
        options.block.border = configuredStyles.block[0].border;
        options.block.borderRadius = configuredStyles.block[0].borderRadius;
        break;
      }
      case 'Quiz Block:2': {
        options.block.border = configuredStyles.block[1].border;
        options.block.borderRadius = configuredStyles.block[1].borderRadius;
        break;
      }
      case 'Quiz Block:3': {
        options.block.border = configuredStyles.block[2].border;
        options.block.borderRadius = configuredStyles.block[2].borderRadius;
        break;
      }
      case 'Title:1': {
        options.header.fontFamily = configuredStyles.header[0].fontFamily;
        options.header.fontSize = configuredStyles.header[0].fontSize;
        options.header.fontWeight = configuredStyles.header[0].fontWeight;
        options.header.lineHeight = configuredStyles.header[0].lineHeight;
        options.header.textDecoration = configuredStyles.header[0].textDecoration;
        break;
      }
      case 'Title:2': {
        options.header.fontFamily = configuredStyles.header[1].fontFamily;
        options.header.fontSize = configuredStyles.header[1].fontSize;
        options.header.fontWeight = configuredStyles.header[1].fontWeight;
        options.header.lineHeight = configuredStyles.header[1].lineHeight;
        options.header.textDecoration = configuredStyles.header[1].textDecoration;
        break;
      }
      case 'Button:1': {
        options.button.normal.border = configuredStyles.button.normal[0].border;
        options.button.normal.color = configuredStyles.button.normal[0].color;
        options.button.normal.fontFamily = configuredStyles.button.normal[0].fontFamily;
        options.button.normal.fontSize = configuredStyles.button.normal[0].fontSize;
        options.button.normal.fontWeight = configuredStyles.button.normal[0].fontWeight;
        options.button.hover.border = configuredStyles.button.hover[0].border;
        options.button.hover.color = configuredStyles.button.hover[0].color;
        break;
      }
      case 'Button:2': {
        options.button.normal.border = configuredStyles.button.normal[1].border;
        options.button.normal.color = configuredStyles.button.normal[1].color;
        options.button.normal.fontFamily = configuredStyles.button.normal[1].fontFamily;
        options.button.normal.fontSize = configuredStyles.button.normal[1].fontSize;
        options.button.normal.fontWeight = configuredStyles.button.normal[1].fontWeight;
        options.button.hover.border = configuredStyles.button.hover[1].border;
        options.button.hover.color = configuredStyles.button.hover[1].color;
        break;
      }
      case 'Questions & Answers:1': {
        options.paragraph.fontFamily = configuredStyles.paragraph[0].fontFamily;
        options.paragraph.fontSize = configuredStyles.paragraph[0].fontSize;
        break;
      }
      case 'Questions & Answers:2': {
        options.paragraph.fontFamily = configuredStyles.paragraph[1].fontFamily;
        options.paragraph.fontSize = configuredStyles.paragraph[1].fontSize;
        break;
      }
      case 'Questions & Answers:3': {
        options.paragraph.fontFamily = configuredStyles.paragraph[2].fontFamily;
        options.paragraph.fontSize = configuredStyles.paragraph[2].fontSize;
        break;
      }
    }
    this.setState({options})
  }

  render() {
    
    const {
      title,
      teaserImage,
      questionItems,
      teaserBackground,
      finalTeaserImage,
      finalTeaserBackground,
      codeModalDisabled,
      teaserImageValid,
      finalTeaserImageValid,
      styles,
      options
    } = this.state;

    const simplePalette = [
      {label: 'black', value: '#000'},
      {label: 'white', value: '#fff'}
    ]
    
    return (
      <AppMain>
        <Container>
          <EditorHeading>That's cool! Create your Quiz right away!</EditorHeading>
          <TextMessage>
            <h3><EditorInfoIcon label='' primaryColor='#244AC2' /> Before you start!</h3>
            <p>for each block provide question and at least 2 answers, and do not forget to specify which answer is correct.</p>
          </TextMessage>
          <Content>
            <Slider>
              <BasicPart
                title={title}
                teaserImage={teaserImage}
                onChangeHandle={this.onChangeHandle}
                removeTitle={this.removeTitle}
                removeTeaserImage={this.removeTeaserImage}
                changeBackgroundTeaser={this.changeBackgroundTeaser}
                simplePalette={simplePalette}
                teaserBackground={teaserBackground}
                setOpacityBackgroundTeaser={this.setOpacityBackgroundTeaser}
                teaserImageValid={teaserImageValid}
              />
              <TransitionGroup>
              {
                questionItems.map((item, index) => (
                  <CSSTransition key={item.id} timeout={300} classNames="item">
                    <QuestionPart
                      itemIndex={index}
                      questionItem={item}
                      questionItemsLength={questionItems.length}
                      changeQuestion={this.changeQuestion}
                      changeImage={this.changeImage}
                      removeQuestion={this.removeQuestion}
                      removeImage={this.removeImage}
                      changeAnswers={this.changeAnswers}
                      removeAnswers={this.removeAnswers}
                      selectRightAnswer={this.selectRightAnswer}
                      removeQuestionItem={this.removeQuestionItem}
                    />
                  </CSSTransition>
                ))
              }
              </TransitionGroup>

              {
                questionItems.length < 5 &&
                <AddQuestionButton
                  onClick={this.addNewQuestion}
                >
                  <AddCircleIcon label='' primaryColor='#6a90e8' />
                  Add Question
                </AddQuestionButton>
              }
              
              <FinalTeaser
                finalTeaserImage={finalTeaserImage}
                simplePalette={simplePalette}
                finalTeaserBackground={finalTeaserBackground}
                setOpacityFinalBackgroundTeaser={this.setOpacityFinalBackgroundTeaser}
                changeFinalBackgroundTeaser={this.changeFinalBackgroundTeaser}
                onChangeHandle={this.onChangeHandle}
                removeFinalTeaserImage={this.removeFinalTeaserImage}
                finalTeaserImageValid={finalTeaserImageValid}
              />
            
              {
                styles && 
                <AllStyles 
                  styles={styles}
                  setColor={this.setColor}
                  setStyle={this.setStyle}
                />
              }
            </Slider>

            <ContentRight>
              <Quiz
                title={title}
                teaserImage={teaserImage}
                finalTeaserImage={finalTeaserImage}
                questionItems={questionItems}
                teaserBackground={teaserBackground}
                finalTeaserBackground={finalTeaserBackground}
                styles={options}
              />
              <CodeModal
                code={getCode(this.state)}
                isDisabled={codeModalDisabled}
              />
            </ContentRight>
          </Content>
        </Container>
      </AppMain>
    )
  }
}

export default withAuth(EditorPage)
