import React from 'react'
import { Field as BaseField } from '@atlaskit/form'
import TextField from '@atlaskit/field-text'

import FieldMessage from './components/FieldMessage'
import { IWithTranslations, withTranslations } from '../../Translation'
import { IFormState } from '../Form'

export interface IRegexField extends IWithTranslations, IFormState {
  regex?: RegExp
  fieldName: string
  label?: string
  placeholder?: string
  fieldProps?: any
  textFieldProps?: TextField
  validMessage?: string
  helpMessage?: string
  showValidLine?: boolean
  validOnType?: boolean
  validationType?: string
  validate?: any
  disabled?: any
}

const Field = ({
  fieldName,
  label,
  placeholder,
  fieldProps,
  textFieldProps,
  getErrorByCode,
  validMessage,
  disabled,
  helpMessage,
  validate,
  submitted,
  validationType,
  validOnType }: IRegexField) => {
  return (
    <BaseField
      defaultValue=""
      name={fieldName}
      validate={validate}
      isRequired={true}
      label={label}
      {...fieldProps}
    >
      {({fieldProps, error, meta}) => (
        <>
          <TextField
            placeholder={placeholder}
            disabled={disabled}
            isLabelHidden
            shouldFitContainer
            maxLength={fieldName === 'email' || fieldName === 'url' || fieldName === 'name' ? '255' : fieldName === 'password' || fieldName === 'password_confirmation' ? '50' : undefined}
            {...{...textFieldProps, ...fieldProps}}
          />
          <FieldMessage 
            error={getErrorByCode && getErrorByCode(error)}
            helpMessage={helpMessage}
            validMessage={
              validationType &&
              (validOnType || submitted) &&
              meta.valid && validMessage
            }
          />
        </>
      )}
    </BaseField>
  )
}

export default withTranslations(Field)
