import React from 'react'
import Modal, { ModalTransition } from '@atlaskit/modal-dialog'

const ConfirmDelete = ({itemId, deleteItem, cancelDeleting}) => {
  return (
    <ModalTransition>
      <Modal 
        actions={[
          {text: 'Delete', onClick: () => deleteItem(itemId)},
          {text: 'Cancel', appearance: 'primary', onClick: cancelDeleting}
        ]}
        appearance='danger'
        heading='Please, confirm'
      >
        <p>Are you sure you want to delete this result?</p>
        <p>This action cannot be aborted.</p>
      </Modal>
    </ModalTransition>
  )
}

export default ConfirmDelete