import React, { Component } from 'react'
import styled from 'styled-components'
import DropdownMenu, { DropdownItemGroup, DropdownItem } from '@atlaskit/dropdown-menu'

import ConfirmDelete from '../components/ParserResultsTable/ConfirmDelete';
import withAuth from '../components/Auth/component/WithAuth'
import Container from '../components/WidthContainer'
import AppMain from '../components/AppMain'
import ParserResultPagination from '../components/ParserResultsPagination'
import { fetchParserResults, deleteResult } from '../api'

const StyledTh = styled.th`
  text-align: center;
  text-transform: uppercase;
  border: solid 1px #ddd;
  padding: 4px 8px;
  :first-child {
    padding-left: 8px;
  }
`
const StyledTd = styled.td`
  text-align: center;
  border: solid 1px #ddd;
  padding: 0 8px;
  :first-child {
    padding-left: 8px;
  }
  button {
    margin: 5px 10px;
  }
`

class ParsedPages extends Component {
  state = {
    items: [
      {
        id: 1,
        url: '',
        results: '',
        parsed_at: '',
        status: '',
        error: [
          {
            code: null,
            message: '',
            recomendation: '',
            errors: ['']
          }
        ]
      }
    ],
    pagination: {},
    delete–°onfirm: null
  }

  componentDidMount() {
    this.getParserResult('/pages?page=1')
  }

  getParserResult = (url:string) => {
    fetchParserResults(url)
    .then(response => this.setParserResults(response))
    .catch(error => console.log(error))
  }

  setParserResults = (data:any) => {
    this.setState({
      items: data.items,
      pagination: data.pagination
    })
  }

  deleteConfirmation = (id:number) => this.setState({delete–°onfirm: id})

  deleteItem = (id:number) => {
    deleteResult(id)
    .then (result => {
      if(result) {
        this.setState({
          items: [...this.state.items.filter(item => item.id !== id)],
        })
      }
    })
  }

  cancelDeleting = () => this.setState({delete–°onfirm: null})

  selectPage = (url:string) => this.getParserResult(url)

  render() {
    const {items, delete–°onfirm, pagination} = this.state
    
    return (
      <AppMain>
        <Container>
          <table>
            <caption>Table of processed pages of sites</caption>
            <tbody>
              <tr>
                <StyledTh>id</StyledTh>
                <StyledTh>url</StyledTh>
                <StyledTh>dates</StyledTh>
                <StyledTh>status</StyledTh>
                <StyledTh>actions</StyledTh>
              </tr>
              {items.map(item => (
                <tr key={item.id}>
                  <StyledTd>{item.id}</StyledTd>
                  <StyledTd>{item.url}</StyledTd>
                  <StyledTd>{item.parsed_at}</StyledTd>
                  <StyledTd>{item.status}</StyledTd>
                  <StyledTd>
                    <DropdownMenu
                      trigger="Select action"
                      triggerType="button"
                      shouldFlip={false}
                      position="bottom right"
                    >
                      <DropdownItemGroup>
                        <DropdownItem onClick={() => this.deleteConfirmation(item.id)}>
                          delete
                        </DropdownItem>
                      </DropdownItemGroup>
                    </DropdownMenu>
                    {
                      delete–°onfirm === item.id &&
                      <ConfirmDelete 
                        itemId={item.id} 
                        deleteItem={this.deleteItem}
                        cancelDeleting={this.cancelDeleting}
                      />
                    }
                  </StyledTd>
                </tr>
              ))}
            </tbody>
          </table>
          <ParserResultPagination pagination={pagination} selectPage={this.selectPage} />
        </Container>
      </AppMain>
    )
  }
}
 
export default withAuth(ParsedPages)