import React, { PureComponent } from 'react'
import Modal, { ModalTransition } from '@atlaskit/modal-dialog'
import styled from 'styled-components'

import CodeButton from './components/CodeButton'
import CodeGenerator from '../CodeGeneration'
import NavLink from '../NavLink'

const Container = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 50px;
`
const Text = styled.p`
  display: block;
  margin: 0 10px;
  font-size: 18px;
  font-weight: bold;
`

class CodeModal extends PureComponent {
  state = { isOpen: false }

  open = () => this.setState({ isOpen: true })

  close = () => this.setState({ isOpen: false })

  render() {
    const { isOpen } = this.state;
    const {code, isDisabled} = this.props;

    const actions = [{ text: 'Close', onClick: this.close }]

    return (
      <>
        <Container>
          <CodeButton
            onClick={this.open} 
            disabled={isDisabled}
            title={isDisabled ? 'Please add a questions, at least\n2 answers and do not forget\nto specify the correct answer.' : undefined}
          >
            Get code
          </CodeButton>
          <Text>or you can create totally</Text>
          <NavLink
            onClick={() => {
              sessionStorage.removeItem('props');
              sessionStorage.removeItem('parsData');
              sessionStorage.removeItem('configured colors');
              sessionStorage.removeItem('configured styles');
            }} 
            to="/">New Quiz</NavLink
          >
        </Container>

        <ModalTransition>
          {isOpen &&
            <Modal
              actions={actions}
              onClose={this.close}
              heading="Great! Now you get your interactive code!"
            >
              <CodeGenerator code={code} />
            </Modal>
          }
        </ModalTransition>
      </>
    );
  }
}

export default CodeModal