import styled from 'styled-components'

const QuestionNumber = styled.div`
  width: 25px;
  height: 25px;
  font-size: 18px;
  text-align: center;
  line-height: 25px;
  background: #fff;
  border: solid 2px #dce8fe;
  border-radius: 50%;
  position: absolute;
  left: -15px;
  top: 50%;
  transform: translateY(-50%);
`
export default QuestionNumber