import styled from 'styled-components'

const TextMessage = styled.div`
  font-weight: bold;
  margin-bottom: 25px;
  background: linear-gradient(to right, #f3f3f3 0%,#dce8fe 43%,#f3f3f3 100%);
  padding: 10px 0;
  text-align: center;
  > h3 {
    > span {
      line-height: 26px;
    }
  }
`
export default TextMessage
